* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

:root {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 62.5%;
}

a:link,
a:visited {
  text-decoration: none;
  color: inherit;
}

a:active,
a:hover {
  text-decoration: underline;
}

input {
  color: inherit;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 30px #508ef4 inset !important;
  -webkit-text-fill-color: #fff;
}

input::placeholder {
  color: #fff;
}

/* utility classes */

.flex-align-center {
  display: flex;
  align-items: center;
}

.flex-align-center i {
  margin: 5px;
}
