.container {
  max-width: 976px;
  margin: 0 auto;
}

.container-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 50px;
  font-size: 1.5rem;
}

.menu-container {
  color: #fff;
  margin: 20px 0;
}

.menu-header {
  text-align: center;
  font-size: 2.5rem;
  position: relative;
}

.menu-header::after {
  content: "";
  height: 5px;
  width: 100%;
  position: absolute;
  bottom: -5px;
  left: 0;
  border-radius: 20px;
  background: #5f5d7e;
}

.menu-header span {
  color: #5f5d7e;
}

.menu-items-container {
  margin: 20px;
  padding: 10px;
  font-size: 1.5rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.menu-items-container .card {
  background: #508ef4;
  width: 200px;
  height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 10px;
  margin: 10px;
  text-transform: capitalize;
}

.menu-items-container .card .card-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.menu-items-container .card .card-bottom {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.menu-items-container .card .card-bottom button {
  width: 80px;
  height: 30px;
  background: #508ef4;
  border: 1px solid #fff;
  color: #fff;
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.3s;
}

.menu-items-container .card .card-bottom button:hover {
  background: #fff;
  border: 1px solid #508ef4;
  color: #508ef4;
}

.container-header-right {
  display: flex;
  justify-content: space-between;
  width: 20%;
  align-items: center;
}

.sort i {
  cursor: pointer;
}

.container svg {
  height: 350px;
  margin: 50px 0;
  width: 100%;
}

.search {
  text-align: center;
}
