.checkout-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
}

table.checkout-table {
  margin: 50px 0;
  border: 1px solid #5f5d7e;
  border-collapse: collapse;
}

table td,
table th {
  padding: 10px;
  border: 1px solid #5f5d7e;
}

.pay-btn {
  color: #fff;
  width: 150px;
  height: 50px;
  border-radius: 50px;
  background: #508ef4;
  border: 0;
  transition: all 0.3s;
  cursor: pointer;
  font-weight: 600;
  margin: 20px;
}

.pay-btn:hover {
  transform: scale(1.2);
}
