.checkout {
  display: flex;
  align-items: center;
  justify-content: space-around;
  background: #5f5d7e;
  color: #fff;
  padding: 20px;
  font-size: 1.5rem;
}

.checkout-btn {
  width: 150px;
  height: 50px;
  border-radius: 50px;
  background: #508ef4;
  border: 0;
  transition: all 0.3s;
  color: inherit;
  cursor: pointer;
  font-weight: 600;
}

.checkout-btn:hover {
  background: #fff;
  color: #508ef4;
}
