.search-input {
  border: 0;
  height: 50px;
  padding: 15px;
  box-sizing: border-box;
  background: #5f5d7e;
  border-bottom: 2px solid #fff;
  transition: all 0.3s;
  width: auto;
  color: #fff;
  border-radius: 25px;
}

.search-input:focus {
  background: #508ef4;
  outline: none;
  width: auto;
}
